.about-main{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 5px 59px 80px 80px;
}
.about-main-left,
.abut-main-right{
    width: 50%;
}
.about-main-rght{
    display: flex;
    justify-content: flex-end;

}
.about-main-left{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.about-link-element{
    color: #000;
}
.about-anime{
    width: 520px;
    max-width: 100%;
    max-height: 100%;
}
.about-details{
    margin: 10px 0 32px 0;
}
.about-sub-head{
    margin: 0;
}
.vector-frame{
    position: absolute;
    z-index: -10;
    left: 0;
    bottom: 0;
    width: 55%;
}
.about-vector{
    display: block;
    max-height: 100%;
    max-width: 100%;
}
@media only screen and (max-width:640px){
    .vector-frame{
        width: 90%;
    }
    .about-head{
        width: 80%;
    }
    .about-head > p{
        font-size: 1.2em;
        line-height: 1.2em;
    }
    .about-main{
        padding: 50px 32px ;
    }
    .about-main-left,
    .about-main-rght
    {
        width: 100%;
    }
}